@import url(https://fonts.googleapis.com/css?family=Nunito|Source+Sans+Pro);
html, body, header, nav, h1, a,
ul, li, strong, main, button, i, em,
section, img, div, h2, p, form, h3, h4,
fieldset, feature, input, textarea,
span, article, footer, time, small, select {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font: inherit;
  color: inherit;
  text-align: inherit;
  text-decoration: inherit;
  vertical-align: inherit;
  box-sizing: inherit;
  background: transparent; }

ul {
  list-style: none; }

img {
  display: block;
  width: 100%;
  height: auto; }

input[type="password"],
input[type="email"],
input[type="text"],
input[type="submit"],
textarea,
button,
select {
  /*
  Get rid of native styling. Read more here:
  http://css-tricks.com/almanac/properties/a/appearance/
  */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

button,
input[type="submit"] {
  cursor: pointer; }

/* Clearfix */
.group:after {
  content: "";
  display: block;
  clear: both; }

.legend, .tooltip {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10px;
  text-transform: capitalize;
  letter-spacing: 0.5px; }

.title {
  font-family: "Nunito", sans-serif;
  font-size: 12px;
  text-transform: capitalize;
  letter-spacing: 0.5px;
  fill: #666; }

#root {
  overflow-x: hidden; }

.vis text:hover {
  cursor: default; }

.content {
  background-color: #F9F9F9;
  padding: 20px 40px; }

.navbar {
  padding: 16px;
  width: 100vw;
  border-bottom: 1px solid #aaa;
  font-family: "Nunito", sans-serif;
  cursor: default; }
  .navbar .title {
    font-size: 24px;
    letter-spacing: 1px;
    text-transform: capitalize;
    font-weight: 300; }
  .navbar .subtitle {
    color: #aaaaaa;
    font-size: 12px;
    font-style: italic;
    padding-left: 10px; }
    .navbar .subtitle a {
      border-bottom: .2px solid #aaa; }

.selector {
  padding-bottom: 20px;
  width: 300px;
  font-size: 16px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 100;
  letter-spacing: 1px; }

.vis {
  background-color: white;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin: 0 auto 20px;
  font-family: "Source Sans Pro", sans-serif; }
  .vis:nth-child(2) {
    margin-right: 20px; }
  .vis:nth-child(3) .bars rect {
    fill: #00A8CE; }

.bars .pork {
  fill: #F3CBCC; }

.bars .poultry {
  fill: #F3DFC7; }

.bars .beef {
  fill: #822B2B; }

.bars .mutton {
  fill: #CF656E; }

.legend {
  text-anchor: end; }
  .legend .article {
    font-size: 10px;
    letter-spacing: 0.75px; }
    .legend .article.active rect {
      stroke: #F4E04D;
      stroke-width: 1.5px; }
    .legend .article.inactive text {
      fill: #aaa; }
    .legend .article.inactive rect {
      opacity: 0.5; }
    .legend .article.pork rect {
      fill: #F3CBCC; }
    .legend .article.poultry rect {
      fill: #F3DFC7; }
    .legend .article.beef rect {
      fill: #822B2B; }
    .legend .article.mutton rect {
      fill: #CF656E; }
    .legend .article:hover {
      cursor: pointer; }

.Axis-Left .tick line {
  stroke: #ddd; }

.Axis-Bottom .tick line {
  display: none; }

.Axis-Bottom .tick text {
  cursor: default; }

.domain {
  display: none; }

.tooltip {
  font-family: "Source Sans Pro", sans-serif;
  letter-spacing: 0.5px;
  padding: 6px 4px;
  border-radius: 5px;
  border: 1px solid #ddd;
  background: rgba(255, 255, 255, 0.9);
  position: absolute;
  display: none; }

